.Card{
    display: flex;
    flex-direction: column;
    background: rgb(25,155,245);
    background: linear-gradient(90deg, rgba(25,155,245,1) 0%, rgb(33, 174, 225) 50%, rgba(23,195,237,1) 100%);
    align-items: center;
    justify-content: center;
    width: 160px;
    height: 200px;
    border-radius: 14px;
    transition: 0.2s ease-in-out;
}
.Icon{
    display: flex;
    justify-content: center;
    height: 30px;
    padding-top: 2cap;

}
.TeamIcons{
    display: flex;
    justify-content: center;
    align-items: center;
}
.Text{
    justify-content: center;
    color: white;
    align-items: center;
}
.Main-Sec{
    display: flex;
    flex-direction: row;
}
.Main-Content{
    padding-left: 40px;
    padding-top: 30px;
    display: flex;
    flex-direction: row;
    gap: 40px;
}
h2{
    padding-top: 30px;
    padding-left: 40px;
}
.Card:hover{
    box-shadow: -4px 11px 15px -3px rgba(7, 145, 250, 0.323);
}