.Card-2{
    display: flex;
    flex-direction: column;
    width: 160px;
    height: 200px;
    justify-content: center;
    border-radius: 12px;
    background-color: rgb(251, 251, 251);    
    box-shadow: -4px 0px 22px 0px rgba(0,0,0,0.1);
}

.Main-Img-2{
    padding:0px 5px 0px 5px;
    width: 145px;
    height: 84px;
    border-radius: 10px;
}
.Text-2{
    padding:0px 5px 5px 5px;
    font-size: 10px;
    
}
.cta-1{
    cursor: pointer;
    color: white;
    padding: 5px;
    margin-left: 10px;
    border-radius: 4px;
    background: rgb(245, 95, 25);
    background: linear-gradient(90deg, rgb(245, 153, 25) 0%, rgb(225, 139, 33) 50%, rgb(237, 173, 23) 100%);
}
