/* Sidebar.css (create a separate CSS file) */
body{
    margin: 0%;
    padding: 0%;
}
.sidebar {
    display: flex;
    flex-direction: column;
    width: 250px;
    height: 100vh;
    justify-content: center;
    align-items: center;
    background-color: #333;
    color: white;
    gap: 20px;
    
  }
  
  .sidebar ul {
    
    list-style: none;
    padding: 0;
  }
  
  .sidebar li {
    
    margin: 10px 0;
  }
  
  .sidebar a {
    color: white;
    text-decoration: none;
  }
  
  .sidebar a:hover {
    background-color: rgb(53, 161, 255);
    color: rgb(255, 255, 255);
    
  }
  .Logout{
    width: 60px;
    height: 60px;
  }
  .button{
    background: rgb(25,155,245);
    background: linear-gradient(90deg, rgba(25,155,245,1) 0%, rgba(77,204,250,1) 50%, rgba(23,195,237,1) 100%);
    width: 60px;
  }
  .userpic{
    width: 100px;
  }
  p{
    text-align: center;

  }