.Card-2{
    display: flex;
    flex-direction: column;
    width: 160px;
    height: 200px;
    justify-content: center;
    border-radius: 12px;
    background-color: rgb(251, 251, 251);    
    box-shadow: -4px 0px 22px 0px rgba(0,0,0,0.1);
}

.Main-Img{
    padding:0px 5px 0px 5px;
    width: 145px;
    border-radius: 10px;
}
.Text-2{
    padding:0px 0px 5px 0px;
    font-size: 12px;
}
.cta{
    cursor: pointer;
    color: white;
    padding: 5px;
    margin-left: 10px;
    border-radius: 4px;
    background: rgb(25,155,245);
    background: linear-gradient(90deg, rgba(25,155,245,1) 0%, rgb(33, 174, 225) 50%, rgba(23,195,237,1) 100%);
}
p{
    padding: 15px 0px 10px 0px;
}
.Card-2:hover{
    box-shadow: -4px 11px 15px -3px rgba(251, 244, 148, 0.481);
}
.Text-3{
    padding: 0px 0px 10px -20px;
}